.about-text h1 {
    margin: 3px;
}

.about-text p {
    font-size: 21px;
    font-weight: 300;
    max-width: 500px;
    width: 100%;
    margin-top: 10px;
}

@media (max-width: 768px) {
    .about-text p {
        font-size: 18px;
    }
}

.t1,
.t2 {
    transform: translateY(-150px);
}

.t3 {
    transform: translateY(150px);
}